.Header {
  clip-path: polygon(0 0, 100% 0, 100% 100%, 0 96%);
  background: linear-gradient(to right, rgba(74, 20, 140, 0.5), rgba(0, 150, 136, 0.5)), url("/static/hero.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  background-position: center;
  background-attachment: fixed;
  height: 100vh;
  width: 100%;
  overflow: hidden;
  display: flex;
  justify-content: center;
  align-items: center; }
  .HeaderContent {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center; }
  .HeaderImgContainer {
    width: 10rem;
    height: 10rem;
    background-color: #fff;
    border-radius: 50%;
    border: 0.3rem solid rgba(255, 255, 255, 0.8);
    margin: 1rem auto;
    overflow: hidden; }
  .HeaderImg {
    width: 100%;
    height: 100%;
    object-fit: cover; }
  .HeaderSocialButton {
    margin-top: .5rem; }
